@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.projectHeader{
    font-size: 1.5em;
    font-style: italic;
    margin: 10px 0 10px 0;
}

  .content-area {
    margin: 30px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .red {
    color:red;
  }
  
  .square {
    background: radial-gradient(circle at bottom center, #FFC837 15px, #FF8008);
    border: 1px solid white;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 44px;
    margin-right: 0px;
    margin-top: 0px;
    padding: 1px;
    text-align: center;
    width: 44px;
    color: black;
    cursor: pointer;
  }
  
  .square:hover{
    border:1px groove black;

  }
  
  .square-full {
    background: radial-gradient(circle at bottom center, #FFC837 15px, #FF8008);
    border: 1px solid white;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 45px;
    height: 44px;
    margin-right: 0px;
    margin-top: 0px;
    padding: 1px;
    text-align: center;
    width: 44px;
    color: black;
  }
  
  .square:focus {
    outline: none;
  }
  
  .square-winner {
    background: radial-gradient(circle at bottom center, #56ab2f 15px, #a8e063);
    border: 1px solid white;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 45px;
    height: 44px;
    margin-right: 0px;
    margin-top: 0px;
    padding: 1px;
    text-align: center;
    width: 44px;
    color:black;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: column;
  }
  
  .game-info {
    margin-left: 20px;
  }
  
  .game-board {
    margin-left: 20px;
  }
  
  
  .squareNEW {
    cursor: pointer;
    border-width: 2px;
    border-color: black;
    will-change: border-color;
  }
  
  .square.top {
    border-bottom-style: solid;
  }
  
  .square.bottom {
    border-top-style: solid;
  }
  
  .square.left {
    clear: both;
    border-right-style: solid;
  }
  
  .square.right {
    border-left-style: solid;
  } 
  
  .row{
    padding-bottom: 0px;
    grid-template: auto/ auto auto auto;
    display: flex;
    }
.flex-parent {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.flex-parent>h1{
  flex:0 1 100%;
  text-align: center;
  

}
.weatherbody {
  background: #4caf50;
  padding: 20px;
  font-family: 'Roboto', sans-serif;
}

.zip-form {
  padding: 15px;
  border: 1px solid #c8e6c9;
  border-radius: 20px;
  background-color: #fff;
}

.zip-form label {
    margin-top: 7px;
    vertical-align: middle;
}

.zip-form input {
    width: 20%;
    margin: 0 10px;
    font-size: 20px;
    vertical-align: middle;
}

.zip-form button {
    padding: 7px 10px;
    background-color: #fff;
    border: 1px solid #000;
    border-radius: 5px;
}

.weather-list {
  margin: 20px 0;
}

.weather-list-item {
  min-width: 100px;
  margin: 0 10px;
  padding: 10px;
  border: 1px solid #c8e6c9;
  border-radius: 20px;
  background-color: #fff;
  text-align: center;
  cursor: pointer;
}

  .weather-list-item :first-child {
    margin-left: 0;
  }

  .weather-list-item :last-child {
    margin-right: 0;
  }

  .weather-list-item > h3 {
    font-weight: normal;
  }

.current-day {
  box-sizing: border-box;
  padding: 20px;
  padding-top: 0;
  border: 1px solid #c8e6c9;
  border-radius: 20px;
  background-color: #fff;
}

.details {
  justify-content: flex-start;
}
  .details > div {
    padding-left: 20px;
  }

  .details > div :first-child {
    padding-left: 0;
  }

.weather {
  font-size: 30px;
  line-height: 1.4;
}
    
  .weather p {
      margin: 0;
  }

  .weather img {
      vertical-align: middle;
  }


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

